<template><section><h1>CorridorGraphics</h1>
<p><code v-pre>vc-graphics-corridor</code> 组件用于加载走廊对象，由一个中心线和宽度定义的形状，与地球的曲率一致。 它可以放置在地面上或高空放置，也可以选择挤压成一个体积。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载走廊</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :name="name1" :description="description" :corridor.sync="corridor1">
          <vc-graphics-corridor :positions="positions1" :material="material1" :width="200000.0"></vc-graphics-corridor>
        </vc-entity>
        <vc-entity ref="entity2" :name="name2" :description="description" :corridor.sync="corridor2">
          <vc-graphics-corridor :positions="positions2" :height="100000.0" :width="200000.0" :cornerType="0" material="GREEN" :outline="true"></vc-graphics-corridor>
        </vc-entity>
        <vc-entity ref="entity3" :name="name3" :description="description" :corridor.sync="corridor3">
          <vc-graphics-corridor :positions="positions3" :material="material3" outlineColor="WHITE" :outline="true" :height="200000.0" :extrudedHeight="100000.0" :width="200000.0" :cornerType="cornerType3"></vc-graphics-corridor>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
