      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity1"</span> <span class="hljs-attr">:name</span>=<span class="hljs-string">"name1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:corridor.sync</span>=<span class="hljs-string">"corridor1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-corridor</span> <span class="hljs-attr">:positions</span>=<span class="hljs-string">"positions1"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span> <span class="hljs-attr">:width</span>=<span class="hljs-string">"200000.0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-corridor</span>&gt;</span>
