          <span class="hljs-attr">material</span>=<span class="hljs-string">"GREEN"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-corridor</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity3"</span> <span class="hljs-attr">:name</span>=<span class="hljs-string">"name3"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:corridor.sync</span>=<span class="hljs-string">"corridor3"</span>&gt;</span>
