          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
          <span class="hljs-attr">:height</span>=<span class="hljs-string">"200000.0"</span>
          <span class="hljs-attr">:extrudedHeight</span>=<span class="hljs-string">"100000.0"</span>
          <span class="hljs-attr">:width</span>=<span class="hljs-string">"200000.0"</span>
