<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,

        <span class="hljs-attr">corridor1</span>: {},
        <span class="hljs-attr">name1</span>: <span class="hljs-string">'Red corridor on surface with rounded corners'</span>,
        <span class="hljs-attr">positions1</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">105.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">105.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35.0</span> }
        ],
        <span class="hljs-attr">material1</span>: {},

        <span class="hljs-attr">corridor2</span>: {},
        <span class="hljs-attr">name2</span>: <span class="hljs-string">'Green corridor at height with mitered corners and outline'</span>,
        <span class="hljs-attr">positions2</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">90.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">95.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">95.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35.0</span> }
        ],
        <span class="hljs-attr">cornerType2</span>: <span class="hljs-number">0</span>,

        <span class="hljs-attr">corridor3</span>: {},
        <span class="hljs-attr">name3</span>: <span class="hljs-string">'Blue extruded corridor with beveled corners and outline'</span>,
        <span class="hljs-attr">positions3</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">80.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">85.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">85.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35.0</span> }
        ],
        <span class="hljs-attr">cornerType3</span>: <span class="hljs-number">0</span>,
        <span class="hljs-attr">material3</span>: {}
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.entity1.createPromise, <span class="hljs-keyword">this</span>.$refs.entity2.createPromise, <span class="hljs-keyword">this</span>.$refs.entity3.createPromise]).then(
        <span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
          instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
        }
      )
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.material1 = Cesium.Color.RED.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.cornerType2 = Cesium.CornerType.MITERED

        <span class="hljs-keyword">this</span>.cornerType3 = Cesium.CornerType.BEVELED
        <span class="hljs-keyword">this</span>.material3 = Cesium.Color.BLUE.withAlpha(<span class="hljs-number">0.5</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 corridor 是否显示。</td>
</tr>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定描述 corridor 位置的经纬度(高度)数组。 <strong>结构：[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 边之间的距离。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 corridor 高度。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 拉伸高度。</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 拉伸高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 corridor 转角样式。<strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 corridor 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> 指定 corridor 的材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 corridor 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'black'</code></td>
<td><code v-pre>optional</code> 指定 corridor 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 corridor 轮廓线宽度。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 corridor 是否接收或者发射每个点光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 随相机距离改变是否显示参数。<strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定 corridor 的贴对象模式。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2, NUMBER_OF_CLASSIFICATION_TYPES: 3</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 corridor 顺序，没有高度和拉伸高度才有效。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CorridorGraphics.html">CorridorGraphics</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',

          corridor1: {},
          name1: 'Red corridor on surface with rounded corners',
          positions1: [
            { lng: 100.0, lat: 40.0 },
            { lng: 105.0, lat: 40.0 },
            { lng: 105.0, lat: 35.0 }
          ],
          material1: {},

          corridor2: {},
          name2: 'Green corridor at height with mitered corners and outline',
          positions2: [
            { lng: 90.0, lat: 40.0 },
            { lng: 95.0, lat: 40.0 },
            { lng: 95.0, lat: 35.0 }
          ],
          cornerType2: 0,

          corridor3: {},
          name3: 'Blue extruded corridor with beveled corners and outline',
          positions3: [
            { lng: 80.0, lat: 40.0 },
            { lng: 85.0, lat: 40.0 },
            { lng: 85.0, lat: 35.0 }
          ],
          cornerType3: 0,
          material3: {}
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise, this.$refs.entity3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material1 = Cesium.Color.RED.withAlpha(0.5)

          this.cornerType2 = Cesium.CornerType.MITERED

          this.cornerType3 = Cesium.CornerType.BEVELED
          this.material3 = Cesium.Color.BLUE.withAlpha(0.5)
        }
      }
    }
  </script>